* {
    scrollbar-gutter: stable;
}

body {
    font-family: Jaldi, Arial, Helvetica, sans-serif;
    font-size: 1.1em;
}

.scrollLock {
    overflow: hidden;
}

.copy {
    line-height: 140%;
    margin-top: 5px;
    margin-bottom: 10px;
}

a {
    color: #009;
    text-decoration-style: dotted;
    text-decoration-thickness: 1px;
}

a:hover {
    text-decoration-style: solid;
}

.interactor {
    color: #009;
    cursor: pointer;
    text-decoration: underline;
    text-decoration-style: dotted;
    text-decoration-skip-ink: all;
    text-decoration-thickness: 1px;
}

.interactor:hover {
    text-decoration-style: solid;
}

#siteLogo {
    display: block;
    margin: auto;
    width: 90%;
    max-width: 1200px;
}

.signature {
    display: inline-block;
    font-family: Caveat, cursive, sans-serif;
    font-size: 140%;
    letter-spacing: -1px;
}
.signature::first-letter {
    font-size: 140%;
    letter-spacing: -2px;
}

@keyframes nav-select {
    from {
        flex-grow: 1;
        background: #eee;
        font-size: 1rem;
        font-weight: 300;
    }

    to {
        flex-grow: 3;
        background: #fff;
        font-size: 1.2rem;
        font-weight: 700;
    }
}

@keyframes nav-deselect {
    from {
        flex-grow: 3;
        background: #fff;
        font-size: 1.2rem;
        font-weight: 700;
    }

    to {
        flex-grow: 1;
        background: #eee;
        font-size: 1rem;
        font-weight: 300;
    }
}


#navbar {
    width: 95%;
    max-width: 1200px;
    margin: auto;
    display: flex;
    align-items: flex-end;
    flex-wrap: nowrap;
    gap: 10px;
    height: 1.5rem;
}

#navbar>a {
    text-align: center;
    text-decoration: none;
    color: #009;
    font-size: 1rem;
    font-weight: 300;

    background: #eee;
    flex-basis: 100px;
    flex-grow: 1;
    border: 1px solid #000;
    border-radius: 5px;
    animation-name: nav-deselect;
    animation-duration: 300ms;
    animation-timing-function: ease-out;
    padding: 4px;
}

#navbar>a:hover {
    font-weight: 700;
}

#navbar>a.active {
    flex-grow: 3;
    font-size: 1.2rem;
    font-weight: 700;
    background: #fff;
    animation-name: nav-select;
    animation-duration: 100ms;
    animation-timing-function: ease-in;
}

.contentHeadline {
    font-size: 2em;
    font-weight: 800;
    margin-bottom: 20px;
}

.contentSubhead {
    display: inline-block;
    font-size: 1.2em;
    padding-top: 5px;
}

.underline {
    border-bottom: 1px solid #999;
}

#content {
    width: 90%;
    max-width: 1200px;
    margin: auto;
    min-height: 200px;
    opacity: 1;
    padding-top: 20px;
    animation-name: content-fadeIn;
    animation-duration: 300ms;
    animation-timing-function: ease-in-out;
}

.contentBody {
    line-height: 120%;
}

@keyframes content-fadeOut {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

@keyframes content-fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


#content.loading {
    opacity: 0;

    animation-name: content-fadeOut;
    animation-duration: 300ms;
    animation-timing-function: ease-in-out;
}


#siteFooter {
    border-top: 1px solid #ccc;
    color: #666;
    display: block;
    width: 90%;
    margin: 75px auto 50px auto;
    max-width: 1200px;
    padding-top: 10px;
    text-align: center;
    clear: both;
}

#siteFooter a {
    color: inherit;
    text-decoration: none;
}

#lightbox {
    display: none;
    position: fixed;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    background: rgba(.3, .3, .3, 0);
    pointer-events: none;
    text-align: center;
}

#lightbox.active {
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: all;
    background: rgba(.3, .3, .3, .8);
    background: linear-gradient(0deg,
            rgba(0, 0, 0, .7) 0%,
            rgba(0, 0, 0, .95) 15%,
            rgba(0, 0, 0, .95) 85%,
            rgba(0, 0, 0, .7) 100%);
}

#lightboxImage {
    display: inline-block;
    background: #fff;
    margin: 0px;
    padding: 0px;
    max-width: 100%;
    height: auto;
}