@font-face {
    font-family: 'CourierPrime';
    src: url("font/CourierPrimeRegular.ttf") format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'CourierPrime';
    src: url("font/CourierPrimeItalic.ttf") format('truetype');
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'CourierPrime';
    src: url("font/CourierPrimeBold.ttf") format('truetype');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'CourierPrime';
    src: url("font/CourierPrimeBoldItalic.ttf") format('truetype');
    font-weight: bold;
    font-style: italic;
}